@tailwind base;
@tailwind components;
@tailwind utilities;

/*
================================================================================
M1st DESIGN SYSTEM - TOKEN ARCHITECTURE DOCUMENTATION
================================================================================

This design system uses a 4-layer token architecture for maximum scalability and maintainability:

LAYER 1: FOUNDATION TOKENS (Variables)
- Raw color values and base design decisions
- Examples: --neutral-100, --neutral-900, --red-600
- Usage: Never use directly in components, only for building semantic tokens

LAYER 2: SEMANTIC TOKENS (Color Intentions)
- Map foundation tokens to semantic meanings
- Examples: --color-background-primary, --color-text-primary, --color-border-primary
- Usage: Used by designers and for building higher-level tokens

LAYER 3: PAGE-LEVEL TOKENS (Page Context) **NEW LAYER**
- Map semantic tokens to specific page usage contexts
- Examples: --page-background, --page-text-primary, --page-surface
- Usage: Perfect for pages, sections, and layout components
- Benefits: Easy theme switching, consistent page-level styling

LAYER 4: COMPONENT TOKENS (Component-Specific)
- Component-specific design decisions (when needed)
- Examples: --button-padding, --card-shadow, --input-border-radius
- Usage: Only when components need specialized styling beyond semantic tokens

BEST PRACTICES:
1. Components should primarily use page-level tokens (Layer 3)
2. Use semantic tokens (Layer 2) for component-specific needs
3. Never use foundation tokens (Layer 1) directly in components
4. Page-level tokens provide the best balance of flexibility and consistency

MIGRATION GUIDE:
- Replace: text-[var(--color-text-primary)] → style={{ color: 'var(--page-text-primary)' }}
- Replace: bg-[var(--color-background-primary)] → style={{ backgroundColor: 'var(--page-background)' }}
- Replace: border-[var(--color-border-primary)] → style={{ borderColor: 'var(--page-border)' }}

======================================================================================
*/

/* =============================================================================
   THEME TRANSITIONS & ACCESSIBILITY
   ============================================================================= */

/* Smooth transitions for theme changes */
.theme-transition,
.theme-transition *,
.theme-transition *::before,
.theme-transition *::after {
  transition: 
    background-color 200ms cubic-bezier(0.4, 0, 0.2, 1),
    border-color 200ms cubic-bezier(0.4, 0, 0.2, 1),
    color 200ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1) !important;
}

/* Respect user's motion preferences */
@media (prefers-reduced-motion: reduce) {
  .theme-transition,
  .theme-transition *,
  .theme-transition *::before,
  .theme-transition *::after {
    transition: none !important;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  :root {
    --color-border-primary: #000000;
    --color-text-primary: #000000;
  }
  
  [data-theme="dark"] {
    --color-border-primary: #ffffff;
    --color-text-primary: #ffffff;
  }
}

/* Focus indicators that work in both themes */
.focus-ring:focus-visible {
  outline: 2px solid var(--color-accent-primary);
  outline-offset: 2px;
  border-radius: 0.25rem;
}

/* =============================================================================
   DESIGN SYSTEM TOKENS
   ============================================================================= */

/* M1st Design System Foundation Tokens */
:root {
  /* ==========================================================================
     EXTENDED NEUTRAL SCALE (Tailwind Neutral + Calculated Middle Values)
     ========================================================================== */
  
  /* Extended Neutral Colors - Based on Tailwind's true neutral palette + calculated middle values */
  --neutral-0: #ffffff;        /* white */
  --neutral-25: #fefefd;       /* middle between white and neutral-50 */
  --neutral-50: #fafafa;       /* neutral-50 */
  --neutral-75: #f7f7f7;       /* middle between neutral-50 and neutral-100 */
  --neutral-100: #f5f5f5;      /* neutral-100 */
  --neutral-150: #ececec;      /* middle between neutral-100 and neutral-200 */
  --neutral-200: #e5e5e5;      /* neutral-200 */
  --neutral-250: #d9d9d9;      /* middle between neutral-200 and neutral-300 */
  --neutral-300: #d4d4d4;      /* neutral-300 */
  --neutral-350: #b8b8b8;      /* middle between neutral-300 and neutral-400 */
  --neutral-400: #a3a3a3;      /* neutral-400 */
  --neutral-450: #8b8b8b;      /* middle between neutral-400 and neutral-500 */
  --neutral-500: #737373;      /* neutral-500 */
  --neutral-550: #646464;      /* middle between neutral-500 and neutral-600 */
  --neutral-600: #525252;      /* neutral-600 */
  --neutral-650: #434343;      /* middle between neutral-600 and neutral-700 */
  --neutral-700: #404040;      /* neutral-700 */
  --neutral-750: #2b2b2b;      /* middle between neutral-700 and neutral-800 */
  --neutral-800: #262626;      /* neutral-800 */
  --neutral-850: #1e1e1e;      /* middle between neutral-800 and neutral-900 */
  --neutral-900: #171717;      /* neutral-900 */
  --neutral-925: #0f0f0f;      /* middle between neutral-900 and neutral-950 */
  --neutral-950: #0a0a0a;      /* neutral-950 */
  --neutral-975: #050505;      /* middle between neutral-950 and black */
  --neutral-1000: #000000;     /* black */

  /* ==========================================================================
     BRAND COLORS
     ========================================================================== */
  
  /* Brand Colors (from M1st logo) */
  --color-brand-primary: #EE3831;                /* M1st brand red - matches M logo */
  --color-brand-primary-hover: #d32f2f;          /* darker red for hover */
  --color-brand-primary-active: #b71c1c;         /* darker red for active */
  
  /* Accent Colors - Warm */
  --color-accent-red: #DC2626;                   /* vibrant red accent */
  --color-accent-magenta: #C2185B;               /* bold magenta accent */
  --color-accent-orange: #EA580C;                /* energetic orange accent */
  --color-accent-yellow: #CA8A04;                /* warm yellow accent */
  
  /* Accent Colors - Cool */
  --color-accent-green: #16A34A;                 /* fresh green accent */
  --color-accent-teal: #0F766E;                  /* sophisticated teal accent */
  --color-accent-blue-light: #0284C7;            /* light blue accent */
  --color-accent-blue: #2563EB;                  /* primary blue accent */
  --color-accent-blue-dark: #1E40AF;             /* deep blue accent */
  --color-accent-purple: #7C3AED;                /* rich purple accent */
  
  /* ==========================================================================
     SEMANTIC COLOR PALETTE - M1ST LIGHT THEME (Using Extended Neutrals)
     ========================================================================== */
  
  /* Background Colors - M1st Theme */
  --color-background-primary: var(--neutral-0);      /* white - pure white primary background */
  --color-background-secondary: var(--neutral-50);   /* subtle background */
  --color-background-tertiary: var(--neutral-100);   /* card backgrounds */
  --color-background-canvas: var(--neutral-0);       /* main canvas - pure white background */
  --color-background-overlay: rgba(255, 255, 255, 0.95);
  
  /* Surface Colors (for cards, modals, elevated content) - M1st Theme  */
  --color-surface-primary: var(--neutral-75);        /* neutral-75 - slightly darker for subtle contrast */
  --color-surface-secondary: var(--neutral-25);      /* very subtle contrast for visual chunking */
  --color-surface-elevated: var(--neutral-0);        /* white - elevated cards */
  --color-surface-sunken: var(--neutral-75);         /* input backgrounds */
  
  /* Text Colors - M1st Theme  */
  --color-text-primary: var(--neutral-900);          /* primary text */
  --color-text-secondary: var(--neutral-600);        /* secondary text */
  --color-text-tertiary: var(--neutral-500);         /* tertiary text */
  --color-text-placeholder: var(--neutral-400);      /* placeholder text */
  --color-text-disabled: var(--neutral-350);         /* disabled text */
  --color-text-inverse: var(--neutral-0);            /* text on dark backgrounds */
  
  /* Border Colors - M1st Theme  */
  --color-border-primary: var(--neutral-200);        /* primary borders */
  --color-border-secondary: var(--neutral-150);      /* subtle borders */
  --color-border-tertiary: var(--neutral-100);       /* very light borders */
  --color-border-focus: var(--color-brand-primary);  /* M1st brand red for focus */
  --color-border-danger: #EE3831;                    /* M1st brand red - danger states */
  
  /* Interactive Colors - M1st Theme Brand System */
  --color-accent-primary: var(--neutral-925);                    /* neutral-925 - dark gray */
  --color-accent-primary-hover: var(--neutral-975);              /* neutral-975 - darker for hover */
  --color-accent-primary-active: var(--neutral-950);             /* neutral-950 - darker for active */
  --color-accent-secondary: var(--neutral-600);                  /* neutral-600 - secondary accent */
  --color-accent-secondary-hover: var(--neutral-700);            /* neutral-700 - secondary hover */
  --color-accent-tertiary: var(--neutral-400);                   /* neutral-400 - tertiary accent for gradients */
  
  /* Status Colors - M1st Theme  */
  --color-success: #059669;                      /* emerald-600 - professional green */
  --color-success-emphasis: #047857;             /* emerald-700 */
  --color-success-subtle: #d1fae5;               /* emerald-100 */
  --color-warning: #d97706;                      /* amber-600 */
  --color-warning-emphasis: #b45309;             /* amber-700 */
  --color-warning-subtle: #fef3c7;               /* amber-100 */
  --color-danger: var(--color-brand-primary);    /* use brand red for danger */
  --color-danger-emphasis: var(--color-brand-primary-hover);
  --color-danger-subtle: #fee2e2;                /* red-100 */
  --color-info: #0891b2;                         /* cyan-600 - professional blue */
  --color-info-emphasis: #0e7490;                /* cyan-700 */
  --color-info-subtle: #cffafe;                  /* cyan-100 */
  
  /* Interactive States - M1st Theme */
  --color-state-hover: rgba(229, 229, 229, 0.5);             /* neutral-200 with opacity */
  --color-state-active: rgba(212, 212, 212, 0.7);            /* neutral-300 with opacity */
  --color-state-focus: rgba(238, 56, 49, 0.12);              /* M1st brand red with opacity */
  --color-state-selected: rgba(238, 56, 49, 0.08);           /* M1st brand red with opacity */
  --color-state-disabled: rgba(163, 163, 163, 0.4);          /* neutral-400 with opacity */
  
  /* ==========================================================================
     PAGE-LEVEL SEMANTIC TOKENS (Layer 4) - Maps semantic tokens to page contexts
     ========================================================================== */
  
  /* Page-level backgrounds - for different page contexts */
  --page-background: var(--color-background-primary);           /* main page background (white in light, neutral-75 in dark) */
  --page-surface: var(--color-surface-secondary);               /* page content surfaces for visual chunking */
  --page-surface-elevated: var(--color-surface-elevated);       /* cards, modals */
  --page-surface-sunken: var(--color-surface-sunken);           /* input fields, wells */
  
  /* Page-level text - for different content hierarchies */
  --page-text-primary: var(--color-text-primary);              /* main content text */
  --page-text-secondary: var(--color-text-secondary);          /* secondary content */
  --page-text-muted: var(--color-text-tertiary);               /* muted/helper text */
  --page-text-inverse: var(--color-text-inverse);              /* text on contrasting backgrounds */
  
  /* Page-level interactive states */
  --page-border: var(--color-border-primary);                  /* default borders */
  --page-border-muted: var(--color-border-secondary);          /* subtle borders */
  --page-focus: var(--color-state-focus);                      /* focus states */
  --page-hover: var(--color-state-hover);                      /* hover states */
  --page-active: var(--color-state-active);                    /* active states */
  --page-sidebar-menu-bg: var(--neutral-150);                  /* sidebar menu item background for hover and active states */
  --page-header-nav-bg: var(--neutral-150);                    /* header navigation item background for hover and active states */
  
  /* Page-level accent colors - for interactive elements */
  --page-accent-primary: var(--color-accent-primary);          /* primary accent */
  --page-accent-primary-hover: var(--color-accent-primary-hover); /* primary accent hover */
  --page-accent-primary-active: var(--color-accent-primary-active); /* primary accent active */
  --page-accent-secondary: var(--color-accent-secondary);      /* secondary accent */
  --page-accent-tertiary: var(--color-accent-tertiary);        /* tertiary accent for gradients */
  
  /* Page-level text colors */
  --page-text-accent-hover: var(--color-accent-primary-hover); /* accent text on hover */
  
  /* Page-level background colors */
  --page-background-container: var(--color-background-primary); /* container backgrounds */
  --page-overlay-backdrop: rgba(0, 0, 0, 0.6);                 /* modal/overlay backdrop */
  
  /* Page-level border colors */
  --page-border-control: var(--color-border-primary);          /* form control borders */
  
  /* Page-level status colors - semantic mapping for page usage */
  --page-success: var(--color-success);                        /* success messages */
  --page-success-emphasis: var(--color-success-emphasis);      /* success emphasis */
  --page-success-subtle: var(--color-success-subtle);          /* subtle success */
  --page-warning: var(--color-warning);                        /* warning messages */
  --page-warning-emphasis: var(--color-warning-emphasis);      /* warning emphasis */
  --page-warning-subtle: var(--color-warning-subtle);          /* subtle warning */
  --page-danger: var(--color-danger);                          /* error messages */
  --page-danger-emphasis: var(--color-danger-emphasis);        /* error emphasis */
  --page-danger-subtle: var(--color-danger-subtle);            /* subtle danger */
  --page-info: var(--color-info);                              /* info messages */
  --page-info-emphasis: var(--color-info-emphasis);            /* info emphasis */
  --page-info-subtle: var(--color-info-subtle);                /* subtle info */

  /* ==========================================================================
     CUSTOM FOCUS SHADOWS (Brand Red)
     ========================================================================== */
  
  /* Focus shadows using our M1st brand red */
  --shadow-focus: 0 0 0 3px rgba(238, 56, 49, 0.12);        /* M1st brand red */
  --shadow-focus-danger: 0 0 0 3px rgba(238, 56, 49, 0.20); /* M1st brand red - stronger */
}

/* =============================================================================
   DARK THEME
   ============================================================================= */

.dark,
[data-theme="dark"] {
  /* ==========================================================================
     INVERTED NEUTRAL SCALE - Dark Theme Semantic Mapping
     ========================================================================== */
  
  /* Invert the neutral scale for dark theme semantic consistency */
  --neutral-0: #000000;        /* black (was white) */
  --neutral-25: #050505;       /* very dark (was very light) */
  --neutral-50: #0a0a0a;       /* dark (was light) */
  --neutral-75: #0f0f0f;       /* darker (was lighter) */
  --neutral-100: #171717;      /* neutral-900 equivalent */
  --neutral-150: #1e1e1e;      /* between 100-200 */
  --neutral-200: #262626;      /* neutral-800 equivalent */
  --neutral-250: #2b2b2b;      /* between 200-300 */
  --neutral-300: #404040;      /* neutral-700 equivalent */
  --neutral-350: #434343;      /* between 300-400 */
  --neutral-400: #525252;      /* neutral-600 equivalent */
  --neutral-450: #646464;      /* between 400-500 */
  --neutral-500: #737373;      /* neutral-500 equivalent */
  --neutral-550: #8b8b8b;      /* between 500-600 */
  --neutral-600: #a3a3a3;      /* neutral-400 equivalent */
  --neutral-650: #b8b8b8;      /* between 600-700 */
  --neutral-700: #d4d4d4;      /* neutral-300 equivalent */
  --neutral-750: #d9d9d9;      /* between 700-800 */
  --neutral-800: #e5e5e5;      /* neutral-200 equivalent */
  --neutral-850: #ececec;      /* between 800-900 */
  --neutral-900: #f5f5f5;      /* neutral-100 equivalent */
  --neutral-925: #f7f7f7;      /* between 900-950 */
  --neutral-950: #fafafa;      /* neutral-50 equivalent */
  --neutral-975: #fefefd;      /* between 950-white */
  --neutral-1000: #ffffff;     /* white (was black) */

  /* ==========================================================================
     SEMANTIC COLOR PALETTE - M1ST DARK THEME (Using Inverted Neutrals)
     ========================================================================== */
  
  /* Background Colors - M1st Dark Theme */
  --color-background-primary: var(--neutral-75);     /* neutral-75 - not pure black, comfortable dark */
  --color-background-secondary: var(--neutral-100);  /* neutral-100 - slightly lighter for contrast */
  --color-background-tertiary: var(--neutral-150);   /* neutral-150 - card hover state */
  --color-background-canvas: var(--neutral-75);      /* neutral-75 - inverted via color system */
  --color-background-overlay: rgba(23, 23, 23, 0.95);
  
  /* Surface Colors - M1st Dark Theme */
  --color-surface-primary: var(--neutral-100);       /* neutral-100 - main sections, slightly lighter than bg */
  --color-surface-secondary: var(--neutral-150);     /* neutral-150 - subtle contrast for chunking */
  --color-surface-elevated: var(--neutral-150);      /* neutral-150 - elevated surfaces (cards, modals) */
  --color-surface-sunken: var(--neutral-50);         /* neutral-50 - sunken surfaces (inputs) */
  
  /* Text Colors - M1st Dark Theme */
  --color-text-primary: #ffffff;                 /* primary text */
  --color-text-secondary: #a0a0a0;               /* secondary text */
  --color-text-tertiary: #6b7280;                /* tertiary text */
  --color-text-placeholder: #6b7280;             /* tertiary text for placeholders */
  --color-text-disabled: #4b5563;                /* disabled text */
  --color-text-inverse: #121212;                 /* text on light backgrounds */
  
  /* Border Colors - M1st Dark Theme */
  --color-border-primary: rgba(255, 255, 255, 0.2);     /* medium border */
  --color-border-secondary: rgba(255, 255, 255, 0.1);   /* light border */
  --color-border-tertiary: rgba(255, 255, 255, 0.1);    /* light border */
  --color-border-focus: #EE3831;                         /* M1st brand red for focus */
  --color-border-danger: #EE3831;                        /* M1st brand red */
  
  /* Interactive Colors - Automatically inherits from light theme via inverted neutrals */
  /* --color-accent-primary: var(--neutral-925) automatically becomes light gray */
  /* --color-accent-primary-hover: var(--neutral-900) automatically becomes lighter */
  /* --color-accent-primary-active: var(--neutral-950) automatically becomes darker */
  --color-accent-secondary: #a0a0a0;             /* secondary text */
  --color-accent-secondary-hover: #ffffff;       /* primary text */
  --color-accent-tertiary: #6b7280;              /* gray-500 - tertiary accent for dark theme */
  
  /* Status Colors - M1st Dark Theme */
  --color-success: #22c55e;                      /* green-500 */
  --color-success-emphasis: #16a34a;             /* green-600 */
  --color-success-subtle: rgba(34, 197, 94, 0.15);   /* green-500 with 15% opacity */
  --color-warning: #f59e0b;                      /* amber-500 */
  --color-warning-emphasis: #d97706;             /* amber-600 */
  --color-warning-subtle: rgba(245, 158, 11, 0.15);  /* amber-500 with 15% opacity */
  --color-danger: #EE3831;                       /* M1st brand red */
  --color-danger-emphasis: #d32f2f;              /* darker M1st red */
  --color-danger-subtle: rgba(238, 56, 49, 0.15);    /* M1st brand red with 15% opacity */
  --color-info: #3b82f6;                         /* blue-500 */
  --color-info-emphasis: #2563eb;                /* blue-600 */
  --color-info-subtle: rgba(59, 130, 246, 0.15);     /* blue-500 with 15% opacity */
  
  /* Interactive States - M1st Dark Theme */
  --color-state-hover: rgba(255, 255, 255, 0.08);    /* white with 8% opacity for subtle hover */
  --color-state-active: rgba(255, 255, 255, 0.12);   /* white with 12% opacity for active */
  --color-state-focus: rgba(238, 56, 49, 0.12);      /* M1st brand red with 12% opacity */
  --color-state-selected: rgba(238, 56, 49, 0.15);   /* M1st brand red with 15% opacity */
  --color-state-disabled: rgba(107, 114, 128, 0.4);  /* tertiary text with 40% opacity */
  
  /* ==========================================================================
     BUTTON COMPONENT TOKENS - M1ST DARK THEME OVERRIDES (Layer 4)
     ========================================================================== */
  
  /* M1st dark theme button adjustments - using Layer 2 semantic tokens */
  --button-ghost-bg-hover: var(--color-state-hover);         /* semantic token */
  --button-ghost-bg-active: var(--color-state-active);       /* semantic token */
  --button-ghost-text: var(--color-text-primary);            /* semantic token */
  
  --button-secondary-bg-hover: var(--color-state-hover);     /* semantic token */
  --button-secondary-bg-active: var(--color-state-active);   /* semantic token */
}

