    --color-danger: #EE3831;                       /* M1st brand red */
    --color-danger-emphasis: #d32f2f;              /* darker M1st red */
    --color-danger-subtle: rgba(238, 56, 49, 0.15);
    --color-info: #3b82f6;                         /* blue-500 */
    --color-info-emphasis: #2563eb;                /* blue-600 */
    --color-info-subtle: rgba(59, 130, 246, 0.15);
    
    /* Interactive States - M1st System Dark Theme */
    --color-state-hover: rgba(255, 255, 255, 0.08);    /* white with 8% opacity for subtle hover */
    --color-state-active: rgba(255, 255, 255, 0.12);   /* white with 12% opacity for active */
    --color-state-focus: rgba(238, 56, 49, 0.12);      /* M1st brand red with 12% opacity */
    --color-state-selected: rgba(238, 56, 49, 0.15);   /* M1st brand red with 15% opacity */
    --color-state-disabled: rgba(107, 114, 128, 0.4);  /* tertiary text with 40% opacity */
  }
}

