/* =============================================================================
   BASE STYLES - CUSTOM OVERRIDES ONLY
   ============================================================================= */

/* Font smoothing (not provided by Tailwind Preflight) */
html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Body with design system tokens */
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Noto Sans", Helvetica, Arial, sans-serif;
  color: var(--color-text-primary);
  background-color: var(--color-background-canvas);
  min-height: 100vh;
}

/* =============================================================================
   COMPONENT-LEVEL DESIGN TOKENS
   ============================================================================= */

:root {
  /* ==========================================================================
     PAGE-LEVEL TOKENS (Application Layer)
     ========================================================================== */
  
  /* Page Structure */
  --page-background: var(--color-background-canvas);      /* Main page background */
  --page-surface: var(--color-surface-primary);           /* Main content surfaces */
  --page-text: var(--color-text-primary);                 /* Main page text */
  --page-text-secondary: var(--color-text-secondary);     /* Secondary page text */
  --page-border: var(--color-border-secondary);           /* Page-level borders */
  
  /* Page Navigation */
  --page-nav-background: var(--color-surface-primary);    /* Navigation background */
  --page-nav-border: var(--color-border-secondary);       /* Navigation borders */
  --page-nav-text: var(--color-text-secondary);           /* Navigation text */
  --page-nav-text-active: var(--color-accent-primary);    /* Active nav text */
  
  /* Page Content Areas */
  --page-header-background: var(--color-surface-primary); /* Page header background */
  --page-sidebar-background: var(--color-surface-primary); /* Sidebar background */
  --page-footer-background: var(--color-background-secondary); /* Footer background */
  --page-main-nav-background: var(--color-surface-secondary); /* Main navigation background */
  
  /* ==========================================================================
     BUTTON COMPONENT TOKENS (Layer 4 - Component Tokens)
     ========================================================================== */
  
  /* Primary Button - Uses Layer 3 (Page-Level Tokens) */
  --button-primary-bg: var(--page-accent-primary);
  --button-primary-bg-hover: var(--page-accent-primary-hover);
  --button-primary-bg-active: var(--page-accent-primary-active);
  --button-primary-text: var(--page-text-inverse);              /* uses page-level token for proper inversion */
  --button-primary-border: var(--page-accent-primary);
  --button-primary-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --button-primary-shadow-hover: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  
  /* Secondary Button - Uses Layer 3 (Page-Level Tokens) */
  --button-secondary-bg: transparent;
  --button-secondary-bg-hover: var(--page-hover);
  --button-secondary-bg-active: var(--page-active);
  --button-secondary-text: var(--page-accent-primary);
  --button-secondary-border: var(--neutral-925);
  
  /* Ghost Button - Uses Layer 3 (Page-Level Tokens) */
  --button-ghost-bg: transparent;
  --button-ghost-bg-hover: var(--page-hover);
  --button-ghost-bg-active: var(--page-active);
  --button-ghost-text: var(--page-text-primary);
  --button-ghost-border: transparent;
  
  /* Danger Button - Uses Layer 3 (Page-Level Tokens) */
  --button-danger-bg: var(--page-danger);
  --button-danger-bg-hover: var(--page-danger-emphasis);
  --button-danger-text: var(--page-text-inverse);
  --button-danger-border: var(--page-danger);
  
  /* Info Button - Uses Layer 3 (Page-Level Tokens) */
  --button-info-bg: var(--page-info);
  --button-info-bg-hover: var(--page-info-emphasis);
  --button-info-text: var(--page-text-inverse);
  --button-info-border: var(--page-info);
  
  /* Success Button - Uses Layer 3 (Page-Level Tokens) */
  --button-success-bg: var(--page-success);
  --button-success-bg-hover: var(--page-success-emphasis);
  --button-success-text: var(--page-text-inverse);
  --button-success-border: var(--page-success);
  
  /* Warning Button - Uses Layer 3 (Page-Level Tokens) */
  --button-warning-bg: var(--page-warning);
  --button-warning-bg-hover: var(--page-warning-emphasis);
  --button-warning-text: var(--page-text-inverse);
  --button-warning-border: var(--page-warning);
  
  /* Button Base Styles */
  --button-border-radius: 0.125rem;
  --button-font-weight-base: 500;
  --button-font-weight-emphasis: 600;
  --button-transition: all 200ms cubic-bezier(0.4, 0, 0.2, 1);
  --button-focus-ring: 0 0 0 3px rgba(238, 56, 49, 0.12);
  --button-disabled-opacity: 0.5;
  
  /* ==========================================================================
     INPUT COMPONENT TOKENS
     ========================================================================== */
  
  --input-bg: var(--color-surface-primary);
  --input-bg-focus: var(--color-surface-primary);
  --input-bg-disabled: var(--color-background-tertiary);
  --input-text: var(--color-text-primary);
  --input-text-placeholder: var(--color-text-placeholder);
  --input-text-disabled: var(--color-text-disabled);
  --input-border: var(--color-border-primary);
  --input-border-focus: var(--color-border-focus);
  --input-border-error: var(--color-border-danger);
  
  /* ==========================================================================
     CARD COMPONENT TOKENS
     ========================================================================== */
  
  --card-bg: var(--color-surface-primary);
  --card-bg-hover: var(--color-surface-elevated);
  --card-border: var(--color-border-secondary);
  
  /* ==========================================================================
     MODAL COMPONENT TOKENS
     ========================================================================== */
  
  --modal-overlay-bg: rgba(0, 0, 0, 0.5);
  --modal-bg: var(--color-surface-primary);
  --modal-border: var(--color-border-primary);
  
  /* ==========================================================================
     NAVIGATION COMPONENT TOKENS
     ========================================================================== */
  
  --nav-bg: var(--color-surface-primary);
  --nav-border: var(--color-border-secondary);
  --nav-link-text: var(--color-text-secondary);
  --nav-link-text-hover: var(--color-text-primary);
  --nav-link-text-active: var(--color-accent-primary);
  --nav-link-bg-hover: var(--color-state-hover);
  --nav-link-bg-active: var(--color-state-selected);
  
  /* ==========================================================================
     LOADING SPINNER COMPONENT TOKENS
     ========================================================================== */
  
  --loading-spinner-bg: var(--page-background);
  --loading-spinner-brand-color: var(--color-brand-primary);
  --loading-spinner-text: var(--page-text-secondary);
  --loading-spinner-simple-border: var(--page-border-muted);
  --loading-spinner-secondary-color: #213d70;              /* M1st secondary spinner color */
}

